*{box-sizing: border-box;}

body{
    margin: 0;
}

.container{
    margin: 1rem;
    text-align: center;
}

.nav{
    background-color: rgb(187, 221, 200);
    color: black;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 2rem;
    padding: 0 1rem;
}

.nav ul{
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
}

.nav a{
    color: inherit;
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;;
}

.site-title{
    font-family: cursive;
    font-size: x-large;
}

.nav li:hover{
    background-color: burlywood;
}

.home{
    background-color: rgb(222, 202, 225);
    font-size: 2rem;
}

.home h2{
    font-size: 25px;
}

.home p{
    font-size: 20px;
}

img{
    width: 30%;
}

.about{
    font-size: 20px;
}

.about p{
    font-size: 1.5rem;
}

